@font-face {
  font-family: 'KorailRoundGothicMedium';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2212@1.0/KorailRoundGothicMedium.woff2') format('woff2');
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: 'TheJamsil5Bold';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2302_01@1.0/TheJamsil5Bold.woff2') format('woff2');
  font-weight: 700;
  font-style: normal;
}

:root {
  --box-margin: 80px;
  --data-size: 80%;
  --text-color: black;
  --text-size: 18px;
  --menu-femily: 'TheJamsil5Bold';
  --list-femily: 'KorailRoundGothicMedium';
}

/* ---------------------Bisic.css--------------------- */
.basic_container {
  width: 100%;
  height: 100%;
  margin-top: 3px;
}
.basic_box {
  width: var(--data-size);
  margin: 0 auto;
  padding: 20px;
  background-color: white;
}
/* ---------------------그리드 내부 요소 스타일--------------------- */
.datagrid_container {
  width: 100%;
  height: 100%;
  float: left;
  margin-top: 3px;
}
.datagrid_box {
  width: var(--data-size);
  height: auto;
  float: left;
  margin-left: 20px;
  margin-top: var(--box-margin);
  background-color: white;
}
