/* ---------------------------공용소스--------------------------- */
.css1 {
  float: left;
  margin-top: 20px;
}
.css2 {
  margin-top: 20px;
}
.fileInput {
  display: none;
}
.centerCell {
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
}
.defaultRow {
  background-color: var(--lightblue-color);
}
.errorRow .MuiDataGrid-cell {
  background-color: var(--red-color);
  color: white;
}
.editingRow .MuiDataGrid-cell {
  background-color: #fffacd !important;
  color: black !important; 
}
.next_page1 {
  text-decoration: none;
  color: var(--black-color);
  font-family: var(--menu-femily);
  font-size: var(--text-size);
  padding: 0;
  margin: 0;
}
.title_text {
  font-family: var(--menu-femily);
  font-size: 25px;
  color: var(--black-color);
}
.company_name{
  font-size: 18px;
}
.nav_sub_box {
  width: 100vw;
  height: 80px;
  background-color: #ffffff;
  box-shadow:
  0 2px 2px rgba(0,0,0,0.11), 
  0 6px 8px rgba(0,0,0,0.11);
}