@font-face {
  font-family: 'KorailRoundGothicMedium';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2212@1.0/KorailRoundGothicMedium.woff2') format('woff2');
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: 'TheJamsil5Bold';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2302_01@1.0/TheJamsil5Bold.woff2') format('woff2');
  font-weight: 700;
  font-style: normal;
}
:root {
  --menu-family: 'TheJamsil5Bold';
  --text-color: white;
  --text-size: 18px;
  --list-family: 'KorailRoundGothicMedium';
}
.list_container {
  width: 250px;
  height: 100vh;
  background-color: var(--blue-color);
  border-radius: 0px 12px 12px 0px;
  float: left;
  z-index: 100;
  transition: width 0.5s ease-in-out;
  display: flex;
  flex-direction: column;
  user-select: none; /* 텍스트 선택 비활성화 */
}
.list_logo {
  padding-top: 20px;
  padding-left: 30px;
  padding-bottom: 50px;
  white-space: nowrap;
  transition: opacity 0.2s ease;
  opacity: 1;
}
.list_container.collapsed .list_logo {
  opacity: 0; 
}
.list_img {
  text-decoration: none;
  color: #ffffff;
  font-size: 18px; 
  font-family: var(--menu-family);
}
.main_menu {
  width: 100%;
  padding: 0;
  margin: 0;
  list-style: none;
  font-size: 18px;
  flex-grow: 1;
  overflow-y: hidden;
  user-select: none; /* 텍스트 선택 비활성화 */
}

.main_menu .menu-item {
  margin: 30px 0;
  padding: 10px 15px;
  transition: background-color 0.2s ease-in-out;
  border-radius: 8px;
  cursor: pointer;
  user-select: none; /* 텍스트 선택 비활성화 */
}

.main_menu .menu-item:first-child {
  margin-top: 0;
}

.menu-link {
  display: flex;
  align-items: center;
  gap: 10px;
  text-decoration: none;
  color: white;
  font-size: var(--text-size);
  font-family: var(--list-family);
  width: 100%;
  overflow: hidden;
  white-space: nowrap;
  user-select: none; /* 텍스트 선택 비활성화 */
}

/* 주메뉴의 호버 효과 제거 */
.main_menu .menu-item:hover > .menu-link {
  background-color: inherit;
  color: inherit;
}

.menu-link .listbar_icon,
.menu-link .listbar_icon2 {
  color: inherit;
  background-color: inherit;
}

/* 리스트바가 접혀 있을 때 아이콘 숨기기 */
.list_container.collapsed .main_menu .menu-link .icon {
  display: none;
}

/* 리스트바가 확장되어 있을 때 아이콘 보이기 */
.list_container.expanded .main_menu .menu-link .icon {
  display: inline;
}

.list_container.collapsed .menu-link,
.list_container.collapsed {
  justify-content: flex-start;
}
.list_container.expanded {
  width: 250px;
}

.list_container.collapsed {
  width: 50px;
}

.toggle-btn1 {
  position: absolute;
  left: 210px;
  top: 105px;
  cursor: pointer;
  z-index: 101;
}
.toggle-btn2 {
  position: absolute;
  left: 15px;
  top: 100px;
  cursor: pointer;
  z-index: 103;
}

.listbar_icon {
  color: #ffffff;
  border-radius: 5px;
}

.listbar_icon2 {
  color: #ffffff;
  border-radius: 5px;
}

.list_container.admin {
  background-color: var(--orange-color);
}

.list_container.admin .menu-link {
  color: white;
}

.sub_menu {
  height: 0;
  opacity: 0;
  overflow: hidden;
  transition: height 0.5s ease-in-out, opacity 0.5s ease-in-out;
  list-style: none;
  padding-left: 15px;
}

.sub_menu.open {
  height: auto;
  opacity: 1;
}

.sub_menu .menu-item {
  margin: 20px 0;
  user-select: none;
}

.sub_menu .menu-item:first-child {
  margin-top: 15px;
}

.sub_menu .menu-item:hover > .menu-link {
  background-color: #ffffff;
  color: #000000;
  border-radius: 8px; 
}

.sub_menu .menu-item .listbar_icon,
.sub_menu .menu-item .listbar_icon2 {
  color: inherit;
  background-color: inherit;
}