@font-face {
  font-family: 'TheJamsil5Bold';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2302_01@1.0/TheJamsil5Bold.woff2') format('woff2');
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: 'KorailRoundGothicMedium';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2212@1.0/KorailRoundGothicMedium.woff2') format('woff2');
  font-weight: 700;
  font-style: normal;
}
:root {
  --background-color: white;
  --menu-femily: 'TheJamsil5Bold';
  --list-femily: 'KorailRoundGothicMedium';
}
.nav_container {
  width: 100%;
  height: 80px;
  z-index: 50;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px 12px;
  background-color: var(--background-color);
  box-shadow:
  0 2px 2px rgba(0,0,0,0.11);
}
.nav_logo {
  padding-top: 5px;
  margin-left: 20px;
}
.nav_menu {
  list-style: none;
  display: flex;
  margin: 0;
  padding-left: 0;
  font-family: var(--menu-femily);
}
.nav_menu li {
  padding: 8px 40px;
}
.nav_auth {
  list-style: none;
  display: flex;
  margin: 0;
  padding: 0;
  font-size: 20px;
  font-family: var(--menu-femily);
  margin-right: 20px;
}
.nav_auth li {
  padding: 8px 12px;
  margin: 0;
}
.nav_auth button {
  background-color: var(--background-color);
  color: var(--black-color);
  border: none;
  cursor: pointer;
}