/* ---------------------------공용소스--------------------------- */
.test1 {
  scale: 1.0;
  margin-top: 20px;
}
.test2 {
  scale: 1.0;
  margin-top: 20px;
}
.fileInput {
  display: none;
}
.centerCell {
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
}
.admin_defaultRow {
  background-color: #fff3d8;
}
.errorRow .MuiDataGrid-cell {
  background-color: #e76262;
  color: white;
}
.editingRow .MuiDataGrid-cell {
  background-color: #aeff19; 
}
.next_page {
  float: right;
  text-decoration: none;
  color: black;
  margin-right: 40px;
  font-family: var(--menu-femily);
  font-size: var(--text-size);
  margin-top: 20px;
}
.befor_page {
  float: right;
  text-decoration: none;
  color: black;
  margin-right: 40px;
  font-family: var(--menu-femily);
  font-size: var(--text-size);
  margin-top: 20px;
}
.next_page1 {
  text-decoration: none;
  color: black;
  font-family: var(--menu-femily);
  font-size: var(--text-size);
  padding: 0;
  margin: 0;
}
.title_text {
  font-family: var(--menu-femily);
  font-size: 25px;
}
.total_text {
  font-family: var(--menu-femily);
  font-size: 25px;
  margin-top: 20px;
}
.nav_sub_box {
  width: 100vw;
  height: 80px;
  background-color: #ffffff;
  box-shadow:
  0 2px 2px rgba(0,0,0,0.11), 
  0 6px 8px rgba(0,0,0,0.11);
}
